html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  background-image: linear-gradient(111.2deg, #e6f80d 0.9%, #80f80d 52.5%, #67cd04 78.2%, #2c970d 99.2%);
  font-family: Arial, sans-serif;
  color: #333;
  min-height: 100vh;
  /* NAVIGATION SECTION */
  /* MAIN SECTION */ }
  body .navigation {
    background-color: #fff;
    left: 0;
    top: 0;
    position: fixed;
    padding: 10px;
    width: 100%; }
    body .navigation .navigation__list {
      display: flex;
      justify-content: center;
      list-style: none;
      padding: 0; }
      body .navigation .navigation__list .navigation__item {
        display: block;
        margin: 0 16px; }
  body .main {
    max-width: 1024px;
    margin: 0 auto;
    /* padding: 0 16px; */
    width: 100%;
    /* MAIN-HEADER SECTION */ }
    body .main .main-header {
      padding-top: 70px;
      text-align: center; }
      body .main .main-header .main-header__title {
        color: #54cf64;
        font-size: 33px;
        text-shadow: 1px 1px 3px black; }
    body .main .grid {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 16px;
      grid-row-gap: 16px;
      padding: 0 16px;
      /* ABOUT SECTION */ }
      body .main .grid .about {
        background-color: greenyellow;
        padding: 16px;
        /* ABOUT-SUBSECTION */ }
        body .main .grid .about .about__title {
          color: #2A9134;
          font-size: 25px; }
        body .main .grid .about .about__paragraph {
          letter-spacing: 2px;
          padding: 0 16px;
          width: 85%; }
        body .main .grid .about .about__paragraph--scrolled {
          overflow: scroll;
          height: 20vh; }
        body .main .grid .about .about__paragraph--hidden, body .main .grid .about .about__list--hidden {
          height: 5vh;
          overflow: hidden; }
        body .main .grid .about .about__link, body .main .grid .about .url-collection__link {
          color: #397367; }
        body .main .grid .about .about__item {
          list-style: circle; }
        body .main .grid .about .url-collection__item {
          list-style-type: upper-roman; }
          body .main .grid .about .url-collection__item .url-collection__image {
            height: 100px;
            width: 150px; }
        body .main .grid .about .about-subsection__title {
          color: #2A9134; }
        body .main .grid .about .about-subsection__link {
          color: #397367; }

/* FOOTER SECTION */
.footer {
  background-image: radial-gradient(circle 644px at 2.8% 48.1%, #20523a 0%, rgba(175, 217, 114, 0.86) 90%);
  color: #333;
  font-weight: bold;
  margin-top: 65px;
  padding: 20px;
  text-align: center; }

@media (min-width: 681px) {
  .hamburger__icon {
    display: none; } }

@media (max-width: 680px) {
  .navigation {
    min-height: 9px; }
    .navigation .navigation__list {
      flex-direction: column;
      display: none; }
      .navigation .navigation__list .navigation__item {
        margin: 16px; }
    .navigation .navigation__list--visible {
      display: flex; }
    .navigation .hamburger__icon {
      border-radius: 2px;
      position: absolute;
      top: 48%;
      right: 7%;
      transform: translate(48%, 7%);
      border: none;
      background: none; }
      .navigation .hamburger__icon .button {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 25px;
        cursor: pointer; }
        .navigation .hamburger__icon .button .hamburger__icon-line {
          display: block;
          width: 25px;
          box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
          border-radius: 3px;
          height: 3px;
          background: #fff;
          transition: all .3s;
          position: relative;
          background-color: #333; }
        .navigation .hamburger__icon .button span + span {
          margin-top: 6px; }
      .navigation .hamburger__icon .active span:nth-child(1) {
        animation: ease .7s top-1 forwards; }
      .navigation .hamburger__icon .not-active span:nth-child(1) {
        animation: ease .7s top-2 forwards; }
      .navigation .hamburger__icon .active span:nth-child(2) {
        animation: ease .7s scaled forwards; }
      .navigation .hamburger__icon .not-active span:nth-child(2) {
        animation: ease .7s scaled-2 forwards; }
      .navigation .hamburger__icon .active span:nth-child(3) {
        animation: ease .7s bottom forwards; }
      .navigation .hamburger__icon .not-active span:nth-child(3) {
        animation: ease .7s bottom-2 forwards; }
  @keyframes top-1 {
    0% {
      top: 0;
      transform: rotate(0); }
    50% {
      top: 12px;
      transform: rotate(0); }
    100% {
      top: 9px;
      transform: rotate(45deg); } }
  @keyframes top-2 {
    0% {
      top: 12px;
      transform: rotate(45deg); }
    50% {
      top: 12px;
      transform: rotate(0deg); }
    100% {
      top: 0;
      transform: rotate(0deg); } }
  @keyframes bottom {
    0% {
      bottom: 0;
      transform: rotate(0); }
    50% {
      bottom: 12px;
      transform: rotate(0); }
    100% {
      bottom: 9px;
      transform: rotate(135deg); } }
  @keyframes bottom-2 {
    0% {
      bottom: 12px;
      transform: rotate(135deg); }
    50% {
      bottom: 12px;
      transform: rotate(0); }
    100% {
      bottom: 0;
      transform: rotate(0); } }
  @keyframes scaled {
    50% {
      transform: scale(0); }
    100% {
      transform: scale(0); } }
  @keyframes scaled-2 {
    0% {
      transform: scale(0); }
    50% {
      transform: scale(0); }
    100% {
      transform: scale(1); } } }

.main-header {
  padding-top: 29px; }

@media (max-width: 740px) {
  .grid {
    grid-template-columns: 1fr; }
  .url-collection__image {
    height: 200px;
    width: 300px; } }

@media (min-width: 741px) {
  .url-collection__image {
    height: 150px;
    width: 250px; } }

@media (min-width: 1080px) {
  .grid {
    grid-template-columns: 1fr 1fr 1fr; }
  .url-collection__image {
    height: 150px;
    width: 250px; } }
